"use client";

import { useState } from "react";
import AdminLayout from "@/components/Admin/AdminLayout";
import AdminTable from "@/components/Admin/AdminTable";
import AdminModal from "@/components/Admin/AdminModal";

interface Project {
  id: number;
  title: string;
  description: string;
  link: string;
  tech: string[];
  createdAt: string;
}

export default function AdminProjectsPage() {
  const [projects, setProjects] = useState<Project[]>([
    {
      id: 1,
      title: "AI Recommendation System",
      description: "Advanced ML-based recommendation engine for e-commerce",
      link: "https://github.com/example/ai-recommendation",
      tech: ["Python", "TensorFlow", "React", "Node.js"],
      createdAt: "2024-01-05",
    },
    {
      id: 2,
      title: "Real-time Analytics Dashboard",
      description: "Dashboard for monitoring real-time data streams",
      link: "https://github.com/example/analytics-dashboard",
      tech: ["React", "D3.js", "WebSocket", "MongoDB"],
      createdAt: "2024-01-12",
    },
    {
      id: 3,
      title: "Blockchain Voting System",
      description: "Secure voting system using blockchain technology",
      link: "https://github.com/example/blockchain-voting",
      tech: ["Solidity", "Web3.js", "React", "Ethereum"],
      createdAt: "2024-01-20",
    },
  ]);

  const [isModalOpen, setIsModalOpen] = useState(false);
  const [editingProject, setEditingProject] = useState<Project | null>(null);
  const [searchTerm, setSearchTerm] = useState("");

  const columns = [
    { key: "title", label: "Title" },
    { key: "description", label: "Description" },
    {
      key: "tech",
      label: "Technologies",
      render: (tech: string[]) => (
        <div className="flex flex-wrap gap-1">
          {tech.slice(0, 2).map((t, index) => (
            <span
              key={index}
              className="rounded-full bg-blue-100 px-2 py-1 text-xs text-blue-800 dark:bg-blue-900 dark:text-blue-200"
            >
              {t}
            </span>
          ))}
          {tech.length > 2 && (
            <span className="rounded-full bg-slate-100 px-2 py-1 text-xs text-slate-600 dark:bg-slate-700 dark:text-slate-300">
              +{tech.length - 2}
            </span>
          )}
        </div>
      ),
    },
    {
      key: "link",
      label: "Link",
      render: (value: string) => (
        <a
          href={value}
          target="_blank"
          rel="noopener noreferrer"
          className="text-sm text-blue-600 underline hover:text-blue-800 dark:text-blue-400 dark:hover:text-blue-300"
        >
          View Project
        </a>
      ),
    },
    { key: "createdAt", label: "Created" },
  ];

  const filteredProjects = projects.filter(
    (project) =>
      project.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
      project.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
      project.tech.some((tech) => tech.toLowerCase().includes(searchTerm.toLowerCase())),
  );

  const handleEdit = (project: Project) => {
    setEditingProject(project);
    setIsModalOpen(true);
  };

  const handleDelete = (project: Project) => {
    if (confirm(`Are you sure you want to delete "${project.title}"?`)) {
      setProjects(projects.filter((p) => p.id !== project.id));
    }
  };

  const handleSave = (projectData: Partial<Project>) => {
    if (editingProject) {
      setProjects(
        projects.map((project) =>
          project.id === editingProject.id ? { ...project, ...projectData } : project,
        ),
      );
    } else {
      const newProject: Project = {
        id: Math.max(...projects.map((p) => p.id)) + 1,
        title: projectData.title || "",
        description: projectData.description || "",
        link: projectData.link || "",
        tech: projectData.tech || [],
        createdAt: new Date().toISOString().split("T")[0],
      };
      setProjects([...projects, newProject]);
    }
    setIsModalOpen(false);
    setEditingProject(null);
  };

  return (
    <AdminLayout title="Manage Projects">
      <div className="space-y-6">
        {/* Header */}
        <div className="flex items-center justify-between">
          <div>
            <h2 className="text-2xl font-semibold text-slate-900 dark:text-white">Projects</h2>
            <p className="text-slate-600 dark:text-slate-400">
              Manage your projects and research work
            </p>
          </div>
          <button
            onClick={() => {
              setEditingProject(null);
              setIsModalOpen(true);
            }}
            className="cursor-pointer rounded-md bg-blue-600 px-4 py-2 font-medium text-white transition-colors hover:scale-105 hover:bg-blue-700"
          >
            Add New Project
          </button>
        </div>

        {/* Search */}
        <div className="rounded-lg bg-white p-4 shadow-md dark:bg-slate-800">
          <input
            type="text"
            placeholder="Search projects..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="w-full rounded-md border border-slate-300 bg-white px-4 py-2 text-slate-900 placeholder-slate-500 focus:border-transparent focus:ring-2 focus:ring-blue-500 dark:border-slate-600 dark:bg-slate-900 dark:text-white dark:placeholder-slate-400"
          />
        </div>

        {/* Table */}
        <AdminTable
          columns={columns}
          data={filteredProjects}
          onEdit={handleEdit}
          onDelete={handleDelete}
        />

        {/* Modal */}
        <AdminModal
          isOpen={isModalOpen}
          onClose={() => {
            setIsModalOpen(false);
            setEditingProject(null);
          }}
          title={editingProject ? "Edit Project" : "Add New Project"}
        >
          <ProjectForm
            project={editingProject}
            onSave={handleSave}
            onCancel={() => {
              setIsModalOpen(false);
              setEditingProject(null);
            }}
          />
        </AdminModal>
      </div>
    </AdminLayout>
  );
}

// Project Form Component
function ProjectForm({
  project,
  onSave,
  onCancel,
}: {
  project: Project | null;
  onSave: (data: Partial<Project>) => void;
  onCancel: () => void;
}) {
  const [formData, setFormData] = useState({
    title: project?.title || "",
    description: project?.description || "",
    link: project?.link || "",
    tech: project?.tech || [],
  });

  const [techInput, setTechInput] = useState("");

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onSave(formData);
  };

  const addTech = () => {
    if (techInput.trim() && !formData.tech.includes(techInput.trim())) {
      setFormData({
        ...formData,
        tech: [...formData.tech, techInput.trim()],
      });
      setTechInput("");
    }
  };

  const removeTech = (techToRemove: string) => {
    setFormData({
      ...formData,
      tech: formData.tech.filter((tech) => tech !== techToRemove),
    });
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div>
        <label className="mb-2 block text-sm font-medium text-slate-700 dark:text-slate-300">
          Title
        </label>
        <input
          type="text"
          value={formData.title}
          onChange={(e) => setFormData({ ...formData, title: e.target.value })}
          className="w-full rounded-md border border-slate-300 bg-white px-4 py-2 text-slate-900 focus:border-transparent focus:ring-2 focus:ring-blue-500 dark:border-slate-600 dark:bg-slate-900 dark:text-white"
          required
        />
      </div>

      <div>
        <label className="mb-2 block text-sm font-medium text-slate-700 dark:text-slate-300">
          Description
        </label>
        <textarea
          value={formData.description}
          onChange={(e) => setFormData({ ...formData, description: e.target.value })}
          rows={3}
          className="w-full rounded-md border border-slate-300 bg-white px-4 py-2 text-slate-900 focus:border-transparent focus:ring-2 focus:ring-blue-500 dark:border-slate-600 dark:bg-slate-900 dark:text-white"
          required
        />
      </div>

      <div>
        <label className="mb-2 block text-sm font-medium text-slate-700 dark:text-slate-300">
          Project Link
        </label>
        <input
          type="url"
          value={formData.link}
          onChange={(e) => setFormData({ ...formData, link: e.target.value })}
          placeholder="https://example.com/project"
          className="w-full rounded-md border border-slate-300 bg-white px-4 py-2 text-slate-900 focus:border-transparent focus:ring-2 focus:ring-blue-500 dark:border-slate-600 dark:bg-slate-900 dark:text-white"
          required
        />
        <p className="mt-1 text-xs text-slate-500 dark:text-slate-400">
          Enter the URL to your project repository or demo
        </p>
      </div>

      <div>
        <label className="mb-2 block text-sm font-medium text-slate-700 dark:text-slate-300">
          Technologies
        </label>
        <div className="mb-2 flex flex-wrap gap-2">
          {formData.tech.map((tech, index) => (
            <span
              key={index}
              className="inline-flex items-center rounded-full bg-blue-100 px-3 py-1 text-sm text-blue-800 dark:bg-blue-900 dark:text-blue-200"
            >
              {tech}
              <button
                type="button"
                onClick={() => removeTech(tech)}
                className="ml-2 text-blue-600 hover:text-blue-800 dark:text-blue-200 dark:hover:text-blue-100"
              >
                Ã—
              </button>
            </span>
          ))}
        </div>
        <div className="flex gap-2">
          <input
            type="text"
            value={techInput}
            onChange={(e) => setTechInput(e.target.value)}
            onKeyPress={(e) => e.key === "Enter" && (e.preventDefault(), addTech())}
            placeholder="Add a technology..."
            className="flex-1 rounded-md border border-slate-300 bg-white px-4 py-2 text-slate-900 focus:border-transparent focus:ring-2 focus:ring-blue-500 dark:border-slate-600 dark:bg-slate-900 dark:text-white"
          />
          <button
            type="button"
            onClick={addTech}
            className="rounded-md bg-blue-600 px-4 py-2 text-white transition-colors hover:bg-blue-700"
          >
            Add
          </button>
        </div>
      </div>

      <div className="flex justify-end space-x-3 pt-4">
        <button
          type="button"
          onClick={onCancel}
          className="px-4 py-2 text-slate-600 transition-colors hover:text-slate-800 dark:text-slate-400 dark:hover:text-slate-200"
        >
          Cancel
        </button>
        <button
          type="submit"
          className="rounded-md bg-blue-600 px-4 py-2 text-white transition-colors hover:bg-blue-700"
        >
          {project ? "Update" : "Create"}
        </button>
      </div>
    </form>
  );
}
